'use strict';
module.exports = angular.module("app.main").controller("mainCtrl", ['$rootScope', '$scope', 'mainData', 'mainServices',function ($rootScope, $scope, mainData,mainServices) {
    var _markon = $rootScope.$on('mark', function (e, i) {
        console.log(i)
        console.log(mainServices)
        var _url = '/siteapi/agent/alert/messagelist';
        var _data = {
            page: 1,
            page_size: 20,
            start_time: '2018-03-11 00:00',
            end_time: '2018-03-26 11:18',
        }

        mainData.ajax(_url, 'get', _data, {
            Accept: "application/json; charset=utf-8",
            token: '1059f8cccc8d39ca5fd4e1d6fcfa376c'
        }).then(function (info) {
            console.log(info)
        })
    });

    $scope.$on('$destroy', function () {
        _markon();
    });



}]).service('mainData', ['$http', function ($http) {
    return {
        ajax: (url, method, data, header) => {
            return $http({
                url: url,
                method: method,
                params: data,
                headers: header ? header : ''
            })
        }
    }
}]).name;


